import React from 'react'
import style from './index.module.styl';
import {Link} from 'react-router-dom';

export default ({verify,register,getMsgCode,msgCodeBtnText,registerBtnState,agreementState}) => (
        <div className={style.subContent}>
                <div className={style['input-box']}>
                        <div className={style.floor}>
                                <input type="text" placeholder='' onInput={({target:{value}}) => verify('username',value)}/>
                        </div>
                        <div className={style.floor}>
                                <input type="text" placeholder='' onInput={({target:{value}}) => verify('msgCode',value)}/>
                                <span className={style['get-msg-code']} onClick={()=>getMsgCode()}>{msgCodeBtnText}</span>
                        </div>
                        <div className={style.floor}>
                                <input type="password" placeholder='' onInput={({target:{value}}) => verify('password',value)}/>
                        </div>
                        <div className={style.floor}>
                                <input type="password" placeholder='' onInput={({target:{value}}) => verify('subPassword',value)}/>
                        </div>
                </div>
                <div className={style.agreement}>
                        <div className={style.autoLogin}>
                                <span onClick={()=>verify('agreementState',!agreementState)}>
                                        {agreementState&&<img alt=""/>}
                                </span>
                                <span></span>
                                <Link to="#"></Link>
                                <span></span>
                                <Link to="#"></Link>
                        </div>
                </div>
                <div className={`${style['register-btn']} ${registerBtnState?style.active:''}`} onClick={()=>register()}>
                        <span></span>
                </div>
                <div className={style.loginBtn}>
                        <span> Smartisan ID</span>
                        <Link to="/user/login">
                                
                        </Link>
                </div>
        </div>
)

